.wp-block-borncreative-header-block {
  position: relative;
  display: block; }
  .wp-block-borncreative-header-block .media {
    width: 100%;
    margin: 0;
    min-height: 50rem; }
    .wp-block-borncreative-header-block .media img {
      display: block;
      width: 100%; }
  .wp-block-borncreative-header-block .copy {
    position: absolute;
    bottom: 1rem;
    left: 0;
    right: 0;
    color: white; }
    .wp-block-borncreative-header-block .copy .copy-hd {
      padding: 0 1rem;
      margin: 0;
      color: white; }
      .wp-block-borncreative-header-block .copy .copy-hd:before {
        display: none; }
    .wp-block-borncreative-header-block .copy .copy-bd {
      margin: 0;
      padding: 0 1rem;
      color: white; }

.wp-block-borncreative-intro-text-block .col-full {
  padding-top: 8rem;
  padding-bottom: 8rem; }

.wp-block-borncreative-intro-text-block .row {
  display: flex; }
  .wp-block-borncreative-intro-text-block .row .column1 {
    flex: 58%;
    padding: 0 1rem; }
  .wp-block-borncreative-intro-text-block .row .column2 {
    flex: 42%;
    padding: 0 1rem; }

.wp-block-borncreative-intro-text-block .copy-bd {
  font-size: 1.5rem; }

.wp-block-borncreative-intro-text-block .headinga,
.wp-block-borncreative-intro-text-block .headingb,
.wp-block-borncreative-intro-text-block .headingc {
  font-size: 1rem;
  font-weight: bold;
  margin: 0; }

.wp-block-borncreative-intro-text-block .linea,
.wp-block-borncreative-intro-text-block .lineb,
.wp-block-borncreative-intro-text-block .linec {
  font-size: 0.8rem;
  margin: 0; }
